*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: sans-serif;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

body {
  font-size: 1.5rem;
  color: black;
  line-height: 1.4;
  font-family: sans-serif;
  background-color: whitesmoke;
}

.main {
  font-size: 1.5rem;
  color: black;
  font-family: sans-serif;
  font-weight: 500;
  overflow-x: hidden;
  width: 100vw;
  height: 100vh;
}
.main::-webkit-scrollbar {
  display: none;
}

.section {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.table-section {
  border-radius: 2px;
  width: 40%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: grey;
  box-shadow: 0 2rem 4rem rgba(0, 0, 0, 0.15);
}

h2 {
  padding-left: 0.5rem;
  font-size: 2rem;
  text-align: center;
}

.form {
  height: 30vh;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0.5rem;
}
.form__input {
  width: 90%;
  height: 3rem;
  margin-top: 2rem;
  border-radius: 2px;
  border: none;
  outline: 1px solid grey;
  padding-left: 0.5rem;
}
.form__button {
  float: left;
  margin-top: 2rem;
  width: 8rem;
  height: 2.8rem;
  text-align: center;
  font-weight: 550;
  font-size: 1.4rem;
  cursor: pointer;
}
.form__list {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.list {
  display: flex;
  justify-content: center;
}
.list__item {
  display: flex;
  width: 90%;
  border-radius: 2px;
  height: 3.5rem;
  align-items: center;
  font-weight: 550;
  justify-content: space-between;
  margin-bottom: 0.85rem;
  transition: var(--transition);
  padding: 0.25rem 1rem;
  font-size: 1.2rem;
  border-radius: var(--radius);
  text-transform: capitalize;
}
.list__item:hover {
  background-color: white;
  color: blue;
  cursor: pointer;
}

.checkbox {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  align-items: start;
  padding-left: 2.7rem;
  justify-content: start;
}
.checkbox__input {
  width: 1.5rem;
  height: 1.5rem;
}/*# sourceMappingURL=index.css.map */